import React,{Component} from 'react'
import { hashHistory, Link } from 'react-router'
import { Row, Col, Breadcrumb, Menu, Icon, Badge, Modal, Button } from 'antd';
import {observer} from 'mobx-react';
import menuStore from 'menuStore';
const MenuItemGroup = Menu.ItemGroup;

@observer
class SpaceFrame extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            basicInfo: {
                avatarImg: "",
                firstName: "",
                accountNum: 0,
                feedback: 0,
                published: 0,
                delivered: 0,
                authStatus: ""
            },
            publishedTypeModal: false
        };
        ['changeFeedbackNum', 'changeAuthStatus', 'showPublisheModal', 'hidePublishModal', 'handleClick', 'goToPublish'].forEach(m=>this[m] = this[m].bind(this))
    }

    goToPublish(type) {
        hashHistory.push(`/fund/space/publish/${type}`);
        this.hidePublishModal();
    }

    showPublisheModal() {
        this.setState({ publishedTypeModal: true })
    }

    hidePublishModal() {
        this.setState({ publishedTypeModal: false });
    }

    componentWillMount() {
        let that = this;
        ajax({
            url: "/info/getBasicInfo",
            callback(data) {
                that.setState({
                    basicInfo: data.basicInfo
                })
            }
        })
    }

    changeAuthStatus(status) {
        this.state.basicInfo.authStatus = status;
        this.setState({ basicInfo: this.state.basicInfo });
    }

    changeFeedbackNum(num) {
        this.state.basicInfo.feedback = num;
        this.setState({ basicInfo: this.state.basicInfo });
    }

    handleClick(e) {
        if(e.key == "publish") {
            this.showPublisheModal();
        } else {
            hashHistory.push(e.key);
        }
        menuStore.changeSpaceMenu(e.key);
    }

    getAuthStatusText(status) {
        switch(status.toString()) {
            case "0":
                return "待提交";
            case "1":
                return "待审核";
            case "2":
                return "已通过";
            case "3":
                return "未通过";
        }
    }

    render() {
        const iconSec = require("biz-imgs/fund/space/icon_security.png");
        const iconPer = require("biz-imgs/fund/space/icon_person.png");
        const iconMobile = require("biz-imgs/fund/space/icon_mobile.png");
        const iconKey = require("biz-imgs/fund/space/icon_key.png");
        let currentPath = menuStore.currentSpaceMenu;

        return (
            <Row className="fundSpaceFrame">
                <Breadcrumb>
                    {this.props.routes.map(item =>
                    <Breadcrumb.Item key={item.breadcrumbName}>{item.breadcrumbName}</Breadcrumb.Item>
                        )}
                </Breadcrumb>
                <Row>
                    <span className="borderLine"></span>
                    <Col span={4} className="fundSpaceMenuFrame">
                        <Row className="basicFrame">
                            <Row className="basic" type="flex" justify="center" align="middle">
                                <Col span={16}>
                                    <Row type="flex" justify="center" className="avatar">
                                        <Col span={20}>
                                            <img src={this.state.basicInfo.avatarImg} />
                                        </Col>
                                    </Row>
                                    <Row className="name">
                                        晚上好，{this.state.basicInfo.firstName}老板
                                    </Row>
                                </Col>
                            </Row>
                            <Row className="iconGroup" type="flex" justify="center" align="middle">
                                <Col span={4} offset={1}>
                                    <img src={iconSec} />
                                </Col>
                                <Col span={4} offset={2}>
                                    <img src={iconPer} /></Col>
                                <Col span={4} offset={2}>
                                    <img src={iconMobile} />
                                </Col>
                                <Col span={4} offset={2}>
                                    <img src={iconKey} />
                                </Col>
                            </Row>
                        </Row>
                        <Row className="menuFrame">
                            <Menu
                                onClick={this.handleClick}
                                selectedKeys={[currentPath]}
                                mode="inline"
                            >
                                <MenuItemGroup key="account" title={<span><Icon type="team" /><span>我的账户</span></span>}>
                                    <Menu.Item key="/fund/space">账户总览</Menu.Item>
                                    <Menu.Item key="/fund/space/recharge">账户充值</Menu.Item>
                                    <Menu.Item key="/fund/space/info">基本信息</Menu.Item>
                                    <Menu.Item key="/fund/space/auth">
                                        我要认证
                                        <Badge count={this.getAuthStatusText(this.state.basicInfo.authStatus)}></Badge>
                                    </Menu.Item>
                                </MenuItemGroup>
                                <MenuItemGroup key="assets" title={<span><Icon type="bar-chart" /><span>我的资金</span></span>}>
                                    <Menu.Item key="publish">发布新资金
                                    </Menu.Item>
                                    <Menu.Item key="/fund/space/draft">资金草稿</Menu.Item>
                                    <Menu.Item key="/fund/space/published">已发布的资金</Menu.Item>
                                    <Menu.Item key="/fund/space/deliver">我的投递</Menu.Item>
                                    <Menu.Item key="/fund/space/feedback">
                                        我的反馈
                                        <Badge count={this.state.basicInfo.feedback} overflowCount={99}></Badge>
                                    </Menu.Item>
                                </MenuItemGroup>
                                <MenuItemGroup key="project" title={<span><Icon type="pay-circle-o" /><span>我的项目</span></span>}>
                                    <Menu.Item key="/fund/space/seen">我查看的项目</Menu.Item>
                                    <Menu.Item key="/fund/space/favorite">我收藏的项目</Menu.Item>
                                    <Menu.Item key="/fund/space/subscribe">我订阅的项目</Menu.Item>
                                </MenuItemGroup>
                            </Menu>
                        </Row>
                    </Col>
                    <Col span={20} className="fundSpaceContentFrame">                 
                        {
                            React.cloneElement(this.props.children, {
                                basicInfo: this.state.basicInfo,
                                changeAuthStatus: this.changeAuthStatus.bind(this),
                                changeFeedbackNum: this.changeFeedbackNum.bind(this)
                            })
                        }
                    </Col>
                </Row>
                <Modal 
                    title="请选择发布类型"
                    visible={this.state.publishedTypeModal}footer={null} 
                    onCancel={this.hidePublishModal}>
                    <Row>
                        <Col span={8}>
                            <Button style={{marginRight: 30}} type="primary" onClick={this.goToPublish.bind(this, "debts")}>债权资金</Button>
                        </Col>
                        <Col span={8}>
                            <Button style={{marginRight: 30}}type="primary" onClick={this.goToPublish.bind(this, "stock")}>股权资金</Button>
                        </Col>
                        <Col span={8}>
                            <Button type="primary" onClick={this.goToPublish.bind(this, "purchase")}>资产收购</Button>
                        </Col>
                    </Row>
                </Modal>
            </Row>
        )
    }
}

module.exports = SpaceFrame;


